<template>
    <a-layout class="form-setting">
        <a-layout-sider :width="260">
            <a-menu @select="onSelectMenu" mode="inline" :default-selected-keys="['main']">
                <a-menu-item key="dialog" title="">
                    <a-icon type="windows" />
                    
                </a-menu-item>
                <a-menu-item key="main" :title="'' + data.main.table_comment">
                    <a-icon type="desktop" />
                    {{data.main.table_comment}}
                </a-menu-item>
                <a-menu-item v-for="(t, idx) in (data.lines||[])" :key="'line-' + (idx + 1)" :title="'' + t.table_comment">
                    <a-icon type="tag" />
                    {{t.table_comment}}
                </a-menu-item>
            </a-menu>
        </a-layout-sider>
        <a-layout-content>
            <a-spin style="height: 100%" :spinning="loading" tip="Loading...">
                <a-table rowKey="id" size="middle" :pagination="false" :columns="columns" :data-source="tableData">
                </a-table>
            </a-spin>
        </a-layout-content>
    </a-layout>
</template>

<script>
const nameMaps = { input: '', textarea: '', number: '', date: '', select: '', dialog: '' };
const EditableCell = {
    render() {
        return this.edit ? 
            (<div class="cell-wrapper">
                <a-select default-value={this.value} style="width: calc(100% - 30px)" size="small" onChange={(val) => this.val = val}>
                    {Object.keys(nameMaps).map((n) => (<a-select-option value={n}>{nameMaps[n]}</a-select-option>))}
                </a-select>
                <a-icon type="check" class="check-icon" onClick={() => {
                    this.$emit('edit', this.val);
                    this.edit = false;
                }} />
            </div>) :
            (<div class="cell-wrapper">
                {nameMaps[this.value]}
                <a-icon type="edit" class="edit-icon" onClick={() => {
                    this.edit = true;
                    this.val = this.value;
                }} />
            </div>);
    },
    props: {
        value: String
    },
    data() {
        return {
            val: '',
            edit: false
        }
    }
};

const EditCell = {
    props: ['row'],
    render() {
        console.log('EditCell.row', this.row);
        let slot = null;
        switch(this.row.category) {
            case "date": {
                slot = (<a-input size="small" style="width: 200px" v-model={this.row.format} placeholder=""/>);
                break;
            }
            case "select": {
                slot = (<a-input size="small" style="width: 200px" v-model={this.row.options} placeholder=","/>);
            }
        }
        return (<div>{slot}</div>);
    }
}

export default {
    props: {
        data: {
            type: Object,
            default() {
                return {}
            }
        }
    },

    data() {
        return {
            loading: false,
            columns: [
                {
                    width: 260,
                    title: '',
                    dataIndex: 'name',
                    customRender: name => {
                        return (<span>{name?.toUpperCase()}</span>);
                    }
                },
                {
                    width: 280,
                    key: 'comment',
                    title: '',
                    dataIndex: 'comment'
                },
                {
                    width: 150,
                    title: '',
                    dataIndex: 'pk',
                    customRender: pk => {
                        return (<span>{pk ? 'Y': 'N'}</span>);
                    }
                },
                {
                    width: 120,
                    key: 'category',
                    title: '',
                    dataIndex: 'category',
                    customRender: (val, row) => {
                        return (<EditableCell value={val} onEdit={(value) =>  row.category = value} />);
                    }
                }, {
                    key: '-',
                    title: '',
                    dataIndex: '-',
                    customRender: (val, row) => {
                        return (<EditCell row={row} />);
                    }
                }
            ],
            tableData: {}
        }
    },

    created() {
        this.tableData = this.data.main.fields;
    },

    methods: {
        onSelectMenu({item, key}) {
            this.loading = true;
            setTimeout(() => {
                if (key == 'main') {
                    this.tableData = this.data.main.fields;
                } else if (key == 'dialog') {
                    alert('~')
                } else {
                    const split = key.split('-');
                    const index = parseInt(split[1]) - 1;
                    this.tableData = this.data.lines[index].fields;
                }
                this.loading = false;
            }, 500);
        }
    }
}
</script>

<style lang="scss">
.form-setting {
    .cell-wrapper > .check-icon {
        float: right;
        margin-top: 4px;
        color: #1890ff;
    }
    .cell-wrapper > .edit-icon {
        float: right;
        display: none;
        margin-top: 3px;
        color: #979797;
    }
    .cell-wrapper:hover > .edit-icon {
        display: unset;
    }
}
</style>
