<template>
    <div id="app">
        <Editjs v-if="ready" :data="mock" :content="context" :events="events" @save="save" />
    </div>
</template>

<script>
import axios from 'axios'
import Editjs from "./components/Editjs.vue"

const baseUrl = process.env.NODE_ENV == 'production' ?
    'http://114.132.201.94:8081' : '/bapi'

export default {
    name: 'App',
    components: {
        Editjs,
    },

    data() {
        return {
            events: [],
            context: '',
            ready: false,
            mock: null
        }
    },
    created() {
        axios.get(baseUrl + '/editjs/mata-info/b3009f91-4b97-11ec-8fd2-b42e99147792/node-b11aw23h0')
            .then(res => {
                if (res.status == 200) {
                    this.mock = res.data.tableInfo;
                    this.events = JSON.parse(res.data.behavior.events.replaceAll('\\\\n', '\\n'));
                    this.context = res.data.behavior.content;
                    this.ready = true;
                }
            });
    },
    methods: {
        save(data) {
            console.log("save data", data);
            axios.put(baseUrl + '/editjs/mata-info/save/b3009f91-4b97-11ec-8fd2-b42e99147792/node-b11aw23h0', data).then(res => {
                console.log('res', res);
            });
        }
    }
}
</script>

<style>
html,
body,
#app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
    height: 100%;
}
</style>
